import React from "react";
import "./searchResult.scss";
import Skeleton from "../skeleton/skeleton";
import { Link } from "react-router-dom";

const SearchResult = ({
  searchResult,
  isLoading,
  currentPage,
  onPageIncrement,
  onPageDecrement,
  pagesQuantity,
}) => {
  // console.log("!!!!searchResult", searchResult);
  if (searchResult && !searchResult.length && !isLoading) {
    return (
      <div className="empty-search-result">
        По Вашему запросу автомобили не найдены
      </div>
    );
  }
  return (
    <div className="search-result">
      <div className="search-result__cars cars-search-result">
        <div className="cars-search-result__row">
          {isLoading
            ? [...new Array(10)].map((_, i) => <Skeleton key={`load_${i}`} />)
            : searchResult.map(({ img, name, price, params, link }, index) => (
                <div
                  key={`car_${index}`}
                  className="cars-search-result__column"
                >
                  <div className="cars-search-result__item item-cars-search-result">
                    <div className="item-cars-search-result__img">
                      {img.includes("http") ? (
                        <img src={img} alt="" />
                      ) : (
                        <div className="img-skeleton"></div>
                      )}
                    </div>
                    <div className="item-cars-search-result__body">
                      <div className="item-cars-search-result__name">
                        {name}
                      </div>
                      <div className="item-cars-search-result__price">
                        {price}
                      </div>
                      <div className="item-cars-search-result__description">
                        {params}
                      </div>
                    </div>
                    <div className="item-cars-search-result__buttons">
                      <Link to={`car/${link}`}>
                        <button className="item-cars-search-result__btn">
                          Подробнее
                        </button>
                      </Link>
                      {/* <div className="item-cars-search-result__btn">
                        Рассчитать стоимость
                      </div> */}
                    </div>
                  </div>
                </div>
              ))}
        </div>
      </div>
      {pagesQuantity && (
        <div className="search-result__pagination pagination-search-result">
          <button
            disabled={currentPage <= 1 || isLoading}
            onClick={onPageDecrement}
            className="pagination-search-result__item pagination-search-result__item_prev"
          >
            Назад
          </button>
          <div className="pagination-search-result__item pagination-search-result__current-page">
            {currentPage} из {pagesQuantity}
          </div>
          <button
            disabled={currentPage > pagesQuantity || isLoading}
            onClick={onPageIncrement}
            className="pagination-search-result__item pagination-search-result__item_next"
          >
            Далее
          </button>
        </div>
      )}
    </div>
  );
};

export default SearchResult;
