import React from "react";
import step1 from "../../assets/icons/steps/01.svg";
import step2 from "../../assets/icons/steps/02.svg";
import step3 from "../../assets/icons/steps/03.png";
import step4 from "../../assets/icons/steps/04.svg";
import step5 from "../../assets/icons/steps/05.svg";
import step6 from "../../assets/icons/steps/06.svg";
import "./steps.scss";

const steps = [
  {
    title: "Параметры",
    text: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Cumque, ratione!",
    img: step1,
    step: 1,
  },
  {
    title: "Договор",
    text: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Cumque, ratione!",
    img: step2,
    step: 2,
  },
  {
    title: "Осмотр",
    text: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Cumque, ratione!",
    img: step3,
    step: 3,
  },
  {
    title: "Транспортировка",
    text: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Cumque, ratione!",
    img: step4,
    step: 4,
  },
  {
    title: "Документы",
    text: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Cumque, ratione!",
    img: step5,
    step: 5,
  },
  {
    title: "Вручение",
    text: "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Cumque, ratione!",
    img: step6,
    step: 6,
  },
];

const Steps = () => {
  return (
    <div className="steps">
      <div className="steps__container _container">
        <div className="steps__title _title">Как мы работаем?</div>
        <div className="steps__body">
          <div className="steps__row">
            {steps.map(({ title, text, img, step }, index) => {
              if (index < 3) {
                return (
                  <div key={step} className="steps__column">
                    <div className="steps__item item-steps">
                      <div className="item-steps__counter">{step}</div>
                      <div className="item-steps__img">
                        <img src={img} alt="" />
                      </div>
                      <div className="item-steps__body">
                        <div className="item-steps__title">{title}</div>
                        <div className="item-steps__text">{text}</div>
                      </div>
                    </div>
                  </div>
                );
              }
              return;
            })}
          </div>
          <div className="steps__row">
          {steps.map(({ title, text, img, step }, index) => {
              if (index > 2) {
                return (
                  <div key={step} className="steps__column">
                    <div className="steps__item item-steps">
                      <div className="item-steps__counter">{step}</div>
                      <div className="item-steps__img">
                        <img src={img} alt="" />
                      </div>
                      <div className="item-steps__body">
                        <div className="item-steps__title">{title}</div>
                        <div className="item-steps__text">{text}</div>
                      </div>
                    </div>
                  </div>
                );
              }
              return;
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Steps;
