import httpService from "./http.service";

const kgCarsEndpoint = "/kg";

const carService = {
  load: async () => {
    const { data } = await httpService.get(kgCarsEndpoint);
    return data;
  },
  search: async (queryString) => {
    const { data } = await httpService.get(kgCarsEndpoint + queryString);
    return data;
  },
};

export default carService;
