import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getCarsBrands,
  getCarsQuantity,
  getDefaultCars,
  getLoadStatus,
  getPagesQuantity,
  loadCars,
  searchCars,
} from "../../store/cars";
import SelectField from "../common/form/selectField.jsx";
import TextField from "../common/form/textField";
import "./catalog.scss";
import SearchResult from "./searchResult/searchResult";
import { validator } from "../../utils/validator";

const initalData = {
  brand: "",
  model: "",
  year: "",
  priceFrom: "",
  priceTo: "",
};
const countryEnum = [
  {
    label: "Киргизия",
    value: "kg",
  },
  {
    label: "Корея",
    value: "kr",
  },
];
const yearEnum = [
  {
    label: "2018",
    value: "2018",
  },
  {
    label: "2019",
    value: "2019",
  },
  {
    label: "2020",
    value: "2020",
  },
  {
    label: "2021",
    value: "2021",
  },
  {
    label: "2022",
    value: "2022",
  },
  {
    label: "2023",
    value: "2023",
  },
];
const validatorConfig = {
  brand: {
    isRequired: {
      message: "Выберите марку",
    },
  },
  model: {
    isRequired: {
      message: "Выберите модель",
    },
  },
  year: {
    isRequired: {
      message: "Выберите год"
    }
  },
  priceFrom: {
    isDigitOnly: {
      message: "Некорректный ввод",
    },
  },
  priceTo: {
    isDigitOnly: {
      message: "Некорректный ввод",
    },
  },
};

const Catalog = () => {
  const [country, setCountry] = useState("kg");
  const [data, setData] = useState(initalData);
  const [searchResult, setSearchResult] = useState([]);
  const [errors, setErrors] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const dispatch = useDispatch();

  const isLoading = useSelector(getLoadStatus());
  const defaultCars = useSelector(getDefaultCars());
  const cars = useSelector(getCarsBrands());
  const pagesQuantity = useSelector(getPagesQuantity());
  const carsQuantity = useSelector(getCarsQuantity());

  useEffect(() => {
    dispatch(loadCars());
  }, []);
  useEffect(() => {
    if (data.brand) {
      setData((prevState) => ({
        ...prevState,
        model: "",
        year: "",
        priceFrom: "",
        priceTo: "",
      }));
    }
  }, [data.brand]);

  const validate = () => {
    const errors = validator(data, validatorConfig);
    setErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleChange = (target) => {
    setData((prevState) => ({
      ...prevState,
      [target.name]: target.value,
    }));
  };
  const handleSumbit = async (e) => {
    e.preventDefault();

    const isValid = validate();
    if (!isValid) return;

    const result = dispatch(searchCars(data));
    setSearchResult(result);
    setCurrentPage(1);

  };
  const handleCountryChange = (currentCountry) => {
    if (currentCountry !== country) {
      setCountry((prevState) => (prevState === "kg" ? "kr" : "kg"));
      setData(initalData);
    }
  };
  const handlePageChange = (pageNumber) => {
    const result = dispatch(searchCars(data, pageNumber));
    setSearchResult(result);
    setCurrentPage(pageNumber);
  };
  const handlePageDecrement = () => handlePageChange(currentPage - 1);
  const handlePageIncrement = () => handlePageChange(currentPage + 1);

  const brands = () => {
    return Object.keys(cars[country]).map((car) => ({
      label: car,
      value: car,
    }));
  };
  const models = (car) => {
    return cars[country][car].models.map((model) => ({
      label: model.text,
      value: model.slug,
    }));
  };

  return (
    <div className={`catalog _${country}`}>
      <div className="catalog__container _container">
        <div className="catalog__title _title">Найти авто</div>
        <div className="catalog__body">
          <div className="catalog__country country-catalog">
            <div className="country-catalog__title">Искать в: </div>
            {countryEnum.map(({ value, label }) => (
              <div
                key={value}
                onClick={() => handleCountryChange(value)}
                className={`country-catalog__item ${
                  value === country ? "_active" : ""
                }`}
              >
                {label}
              </div>
            ))}
          </div>
          <form
            onSubmit={(e) => handleSumbit(e)}
            className="catalog__filter filter-catalog"
          >
            <div className="filter-catalog__item">
              <SelectField
                label="Марка"
                onChange={handleChange}
                options={brands()}
                name="brand"
                value={data.brand}
                placeholder="Марка"
                error={errors.brand}
              />
            </div>
            {data.brand && (
              <>
                <div className="filter-catalog__item">
                  <SelectField
                    label="Модель"
                    onChange={handleChange}
                    options={models(data.brand.value)}
                    name="model"
                    value={data.model}
                    placeholder="Модель"
                    error={errors.model}
                  />
                </div>
                <div className="filter-catalog__item filter-catalog__item_year">
                  <SelectField
                    label="Год от"
                    onChange={handleChange}
                    options={yearEnum}
                    name="year"
                    value={data.year}
                    placeholder="Год"
                    error={errors.year}
                  />
                </div>
                <div className="filter-catalog__item">
                  <TextField
                    placeholder="Цена от, ₽"
                    label="Цена от, ₽"
                    value={data.priceFrom}
                    name="priceFrom"
                    onChange={handleChange}
                    error={errors.priceFrom}
                  />
                </div>
                <div className="filter-catalog__item">
                  <TextField
                    placeholder="Цена до, ₽"
                    label="Цена до, ₽"
                    value={data.priceTo}
                    name="priceTo"
                    onChange={handleChange}
                    error={errors.priceTo}
                  />
                </div>
              </>
            )}
            <div className="filter-catalog__item filter-catalog__item_search">
              <button
                disabled={isLoading}
                className="filter-catalog__search"
                type="submit"
              >
                Искать
              </button>
            </div>
          </form>
          {carsQuantity > 0 && (
            <div
              className={`catalog-items-quantity ${
                isLoading ? "_loading" : ""
              }`}
            >
              Найдено {carsQuantity} авто
            </div>
          )}
          {
            <SearchResult
              isLoading={isLoading}
              searchResult={searchResult.length ? searchResult : defaultCars}
              currentPage={currentPage}
              onPageIncrement={handlePageIncrement}
              onPageDecrement={handlePageDecrement}
              pagesQuantity={pagesQuantity}
            />
          }
        </div>
      </div>
    </div>
  );
};

export default Catalog;
