import { createSlice } from "@reduxjs/toolkit";
import carsBrandsKg from "../initialData/carsBrands_kg.json";
import carsBrandsKr from "../initialData/carsBrands_kr.json";
import carService from "../services/car.service";
import localStorageService from "../services/localStorage.service";

const carsSlice = createSlice({
  name: "cars",
  initialState: {
    carsParams: {
      brands: {
        kg: carsBrandsKg,
        kr: carsBrandsKr,
      },
    },
    cars: [],
    currentCar: {},
    pagesQuantity: "",
    carsQuantity: "",
    errors: null,
    isLoading: false,
  },
  reducers: {
    carsLoad: (state) => {
      state.isLoading = true;
    },
    carsLoaded: (state, action) => {
      state.cars = action.payload;
      state.isLoading = false;
    },
    carsLoadFailed: (state, action) => {
      state.errors = action.payload;
      state.isLoading = false;
    },
    pagesQuantityLoaded: (state, action) => {
      state.pagesQuantity = action.payload;
    },
    carsQuantityLoaded: (state, action) => {
      state.carsQuantity = action.payload;
    },
    currentCarLoaded: (state, action) => {
      state.currentCar = action.payload;
    },
  },
});

const { reducer: carsReducer, actions } = carsSlice;
const {
  carsLoad,
  carsLoaded,
  carsLoadFailed,
  pagesQuantityLoaded,
  carsQuantityLoaded,
  currentCarLoaded,
} = actions;

export const loadCars = () => async (dispatch) => {
  dispatch(carsLoad());
  try {
    const { cars, pagesQuantity, carsQuantity } = await carService.load();

    dispatch(carsLoaded(cars));
    dispatch(pagesQuantityLoaded(pagesQuantity));
    dispatch(carsQuantityLoaded(carsQuantity));
    localStorageService.set("cars", cars);
  } catch (error) {
    dispatch(carsLoadFailed(error));
  }
};
export const searchCars =
  (data, page = 1) =>
  async (dispatch) => {
    dispatch(carsLoad());
    try {
      const { cars, pagesQuantity, carsQuantity } = await carService.search(
        "/search/?" + getQueryString({ ...data, page })
      );

      dispatch(carsLoaded(cars));
      dispatch(pagesQuantityLoaded(pagesQuantity));
      dispatch(carsQuantityLoaded(carsQuantity));
    } catch (error) {
      dispatch(carsLoadFailed(error));
    }
  };
export const loadCarById = (carId) => async (dispatch) => {
  try {
    const { images, configuration } = await carService.search(
      "/details/" + carId
    );
    dispatch(currentCarLoaded({ images, configuration }));
  } catch (error) {}
};

export const getCarsBrands = () => (state) => {
  return state.cars.carsParams.brands;
};

export const getDefaultCars = () => (state) => state.cars.cars;
export const getLoadStatus = () => (state) => state.cars.isLoading;
export const getPagesQuantity = () => (state) => state.cars.pagesQuantity;
export const getCarsQuantity = () => (state) => state.cars.carsQuantity;
export const getCarById = (carId) => (state) => {
  const carsArray = localStorageService.get("cars");
  if (!carsArray) return null;

  const { name, price, params } = carsArray.find((c) => c.link === carId);
  const { images, configuration } = state.cars.currentCar;

  if (images && configuration) {
    return {
      name,
      price,
      params,
      images,
      configuration,
    };
  }
};

function getQueryString({ brand, model, year, priceFrom, priceTo, page }) {
  let queryString = `brand=${brand.value}&model=${model.value}&page=${page}`;
  if (year) queryString += `&year=${year.value}`;
  if (priceFrom) queryString += `&priceFrom=${priceFrom}`;
  if (priceTo) queryString += `&priceTo=${priceTo}`;

  return queryString;
}

export default carsReducer;
