import React from "react";

import icon1 from "../../assets/icons/guarantees/01.svg";
import icon2 from "../../assets/icons/guarantees/02.svg";
import icon3 from "../../assets/icons/guarantees/03.svg";
import icon4 from "../../assets/icons/guarantees/04.svg";

import "./guarantees.scss";

const guarantees = [
  {
    img: icon1,
    title: "Договор",
    text: "Lorem Lorem LoremmLorem Lorem",
  },
  {
    img: icon2,
    title: "Страхование транспортировки",
    text: "Lorem Lorem LoremmLorem Lorem",
  },
  {
    img: icon3,
    title: "Юридическая чистота",
    text: "Lorem Lorem LoremmLorem Lorem",
  },
  {
    img: icon4,
    title: "Опыт более 5 лет",
    text: "Lorem Lorem LoremmLorem Lorem",
  },
];

const Guarantees = () => {
  return (
    <div className="guarantees">
      <div className="guarantees__container _container">
        <div className="guarantees__title _title">Наши гарантии</div>
        <div className="guarantees__row">
          {guarantees.map(({ img, title, text }) => (
            <div key={img} className="guarantees__column">
              <div className="guarantees__item item-guarantees">
                <div className="item-guarantees__img">
                  <img src={img} alt="" />
                </div>
                <div className="item-guarantees__body">
                  <div className="item-guarantees__title">{title}</div>
                  <div className="item-guarantees__text">{text}</div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Guarantees;
